import { Graph, Cell } from '@antv/x6';
import { DagreLayout } from '@antv/layout';
import result3 from './result3.json';
import { useEffect, useRef } from 'react';
import { initGraph } from './logic';
import { useSqlStore } from '@/models/sql';

export const LINE_HEIGHT = 24;
export const NODE_WIDTH = 150;
export const COLOR_MAP = {
  fontColor: '#ffffff',
  hoverBg: '#e5c2ff',
  activeBg: '#1febf6',
  defaultBg: '#ebfdec',
  defaultEdge: '#A2B1C3',
  sourceTable: {
    primary: '#fa541c',
    bg: '#fff2e8',
  },
  targetTable: {
    primary: '#a0d911',
    bg: '#fcffe6',
  },
  selectColumns: {
    primary: '#1677ff',
    bg: '#e6f4ff',
  },
  viewTable: {
    primary: '#eb2f96',
    bg: '#fff0f6',
  },
};

initGraph();

function SqlFlowView2() {
  const graphRef = useRef<Graph>();
  const init = () => {
    graphRef.current = new Graph({
      container: document.getElementById('container')!,
      autoResize: true,
      panning: true,
      interacting: {
        nodeMovable: true, // 节点是否可以被移动
        vertexAddable: false, // 边的路径点是否可以被删除
        vertexDeletable: false, // 是否可以添加边的路径点
        vertexMovable: false, // 边的路径点是否可以被移动
        arrowheadMovable: false, // 边的起始/终止箭头（在使用 arrowhead 工具后）是否可以被移动
        edgeLabelMovable: false, // 边的标签是否可以被移动
        edgeMovable: false, // 边是否可以被移动
        magnetConnectable: false, // 当在具有 magnet 属性的元素上按下鼠标开始拖动时，是否触发连线交互。
      },
      mousewheel: true,
      connecting: {
        router: {
          name: 'er',
          args: {
            offset: 25,
            direction: 'H',
          },
        },
      },
    });

    generate();
  };

  const generate = () => {
    const cells: Cell[] = [];
    const res1 = result3.source.map((item, index) => {
      return {
        id: item.name,
        shape: item.type,
        label: item.name,
        width: 150,
        height: 24,
        ports: item.columns.map((_item) => ({
          id: item.name + '_' + _item,
          group: 'list',
          attrs: {
            portNameLabel: {
              text: _item,
            },
          },
        })),
      };
    });
    const res2 = result3.statements.map((item, index) => {
      return [
        ...item?.mappings.map((_item, _index) => {
          return {
            id: index + '-' + _index,
            shape: 'edge',
            source: {
              cell: item.source,
              port: item.source + '_' + _item.sourceColumn,
            },
            target: {
              cell: item.target,
              port: item.target + '_' + _item.targetColumn,
            },
            connector: { name: 'rounded' },
            attrs: {
              line: {
                stroke: COLOR_MAP.defaultEdge,
                strokeWidth: 1,
                // sourceMarker: {
                //   name: 'circle',
                //   size: 2,
                // },
                targetMarker: {
                  name: 'classic',
                  size: 6,
                },
              },
            },
            zIndex: 0,
          };
        }),
      ];
    });

    [...res1, ...res2.flat(Infinity)].forEach((item: any) => {
      if (item.shape === 'edge') {
        cells.push(graphRef.current.createEdge(item));
      } else {
        cells.push(graphRef.current.createNode(item));
      }
    });

    // 渲染图形
    graphRef.current.resetCells(cells);
    graphRef.current.centerPoint();
    applyDagreLayout(graphRef.current);
  };
  const applyDagreLayout = (graph: Graph) => {
    const nodes = graph.getNodes();
    const edges = graph.getEdges();

    const dagreLayout = new DagreLayout({
      type: 'dagre',
      rankdir: 'LR',
      align: 'UL',
      ranksep: 100,
      nodesep: 100,
      controlPoints: true,
    });

    const model = {
      nodes: nodes.map((node) => ({
        id: node.id,
        width: node.size().width,
        height: node.size().height,
      })),
      edges: edges.map((edge) => ({
        source: edge.getSourceCellId(),
        target: edge.getTargetCellId(),
      })),
    };

    const newPositions = dagreLayout.layout(model);

    newPositions.nodes.forEach((node) => {
      const cell = graph.getCellById(node.id);
      if (cell.isNode()) {
        // @ts-ignore
        cell.setPosition(node.x, node.y);
      }
    });

    graph.centerPoint();
  };
  const handlePortMouseEnter = ({ node, view, cell, port }) => {
    console.log('handlePortMouseEnter', node, view, port);
    useSqlStore.setState({
      highlightWords: ['product_code', 'policy_no', 'user_id'],
    });
    cell.setPortProp(port, 'attrs/rect', {
      fill: COLOR_MAP.hoverBg,
    });
    const connectedEdges = graphRef.current.getConnectedEdges(cell, { incoming: true, outgoing: true }).filter((edge) => edge.getSourcePortId() === port || edge.getTargetPortId() === port);
    connectedEdges.forEach((edge) => {
      // 高亮边
      edge.attr('line/stroke', COLOR_MAP.hoverBg);

      // 高亮边的源端口和目标端口
      const sourceCell = edge.getSourceCell();
      const targetCell = edge.getTargetCell();
      const sourcePort = edge.getSourcePortId();
      const targetPort = edge.getTargetPortId();

      if (sourceCell && sourcePort && sourceCell.isNode()) {
        sourceCell.setPortProp(sourcePort, 'attrs/rect', {
          fill: COLOR_MAP.hoverBg,
        });
      }

      if (targetCell && targetPort && targetCell.isNode()) {
        targetCell.setPortProp(targetPort, 'attrs/rect', {
          fill: COLOR_MAP.hoverBg,
        });
      }
    });
  };

  const handlePortMouseLeave = ({ cell, port, node }) => {
    console.log('handlePortMouseLeave');
    useSqlStore.setState({
      highlightWords: [],
    });
    cell.setPortProp(port, 'attrs/rect', {
      fill: COLOR_MAP[node.shape].bg,
    });

    // 获取与当前端口相连的所有边
    const connectedEdges = graphRef.current.getConnectedEdges(cell, { incoming: true, outgoing: true }).filter((edge) => edge.getSourcePortId() === port || edge.getTargetPortId() === port);

    connectedEdges.forEach((edge) => {
      // 恢复边的颜色
      edge.attr('line/stroke', COLOR_MAP.defaultEdge); // 假设默认边的颜色是黑色，根据实际情况调整

      // 恢复边的源端口和目标端口颜色
      const sourceCell = edge.getSourceCell();
      const targetCell = edge.getTargetCell();
      const sourcePort = edge.getSourcePortId();
      const targetPort = edge.getTargetPortId();

      const sourceNode = edge.getSourceNode();
      const targetNode = edge.getTargetNode();

      if (sourceCell && sourcePort && sourceCell.isNode()) {
        sourceCell.setPortProp(sourcePort, 'attrs/rect', {
          fill: COLOR_MAP[sourceNode.shape].bg,
        });
      }

      if (targetCell && targetPort && targetCell.isNode()) {
        targetCell.setPortProp(targetPort, 'attrs/rect', {
          fill: COLOR_MAP[targetNode.shape].bg,
        });
      }
    });
  };

  const handlePortClick = ({ cell, port }) => {
    console.log('handlePortClick');
    cell.setPortProp(port, 'attrs/rect', {
      fill: COLOR_MAP.activeBg,
    });
  };

  const handleGraphMouseEnter = (e) => {
    console.log('handleGraphMouseEnter');
    useSqlStore.setState({
      highlightWords: [],
    });
    const cells = graphRef.current.getCells();
    cells.forEach((c) => {
      if (c.isNode()) {
        const ports = c.getPorts();
        ports.forEach((p) => {
          c.setPortProp(p.id, 'attrs/rect', {
            fill: COLOR_MAP[c.shape].bg,
          });
        });
      }
    });
  };

  useEffect(() => {
    init();
  }, []);

  useEffect(() => {
    console.log('注册');
    graphRef.current.on('node:port:mouseenter', handlePortMouseEnter);
    graphRef.current.on('node:port:mouseleave', handlePortMouseLeave);
    // graphRef.current.on('node:port:click', handlePortClick);
    graphRef.current.on('graph:mouseenter', handleGraphMouseEnter);

    return () => {
      graphRef.current.off('node:port:mouseenter', handlePortMouseEnter);
      graphRef.current.off('node:port:mouseleave', handlePortMouseLeave);
      // graphRef.current.off('node:port:click', handlePortClick);
      graphRef.current.off('graph:mouseenter', handleGraphMouseEnter);
    };
  }, []);

  return (
    <div
      style={{
        width: '100%',
        height: '100%',
      }}
    >
      <div id="container" />
    </div>
  );
}

export default SqlFlowView2;
